<template>
  <div id="Inputs">
    <form id="Content" @change="change()">
      <div class="inputs">
        <img src="../../assets/images/ZhangHao.svg" class="Pictures" />
        <input
          type="text"
          value=""
          placeholder="请输入学生卡ID"
          class="TrueInputs"
          v-model="id"
          name="id"
        />
      </div>
      <div class="inputs" id="Password">
        <img src="../../assets/images/MiMa.svg" class="Pictures" />
        <input
          type="password"
          value=""
          placeholder="请输入密码"
          class="TrueInputs"
          v-model="password"
          name="pwd"
        />
      </div>
      <img
        src="../../assets/images/CuoWu.svg"
        width="20pt"
        height="20pt"
        id="Picture_Error"
      />
    </form>
    <div id="Error_s">
      <p id="Error_text">密码错误</p>
    </div>
    <div id="Button_s">
      <button type="button" class="Button" @click="login()" style="margin-right:10pt;">登录</button>
      <p class="logon" style="display:flex;">还没账号,点击<router-link to="Logon" style="text-decoration:none;"><p class="logon" style="color:blue;">注册?</p></router-link></p>
    </div>
  </div>
</template>
<script>
export default {
  name: "Inputs",
  data() {
    return {
      id: "",
      password: "",
      background: "",
    };
  },
  mounted() {
    this.background = document.getElementsByClassName("Button")[0];
  },
  methods: {
    login: function () {
      fetch(
         "http://180.76.100.8/users/login/"+this.id+"/"+this.password,
        {
          method: "GET",
        }
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.flag==true&&res.data!=={}) {
          localStorage.setItem("userId",this.id);
        this.$router.push({ path: "Homepage" });
      }else{
        let Picture_Error = document.getElementById("Picture_Error");
        let Error_text = document.getElementById("Error_text");
        let Password = document.getElementById("Password");
        Picture_Error.style.display = "block";
        Error_text.style.display = "block";
        Password.style.border = "1.5px red solid";
        this.password = "";
        setTimeout(() => {
          Picture_Error.style.display = "none";
          Error_text.style.display = "none";
          Password.style.border = "1.5px #B3ACC1 solid";
        }, 1500);
      } 
        });
        
    },
    change: function () {
      if (this.id !== "" || this.password !== "") {
        this.background.style = "background:#5A5564;";
      } else {
        this.background.style = "background:#BDB7C9;";
      }
    },
    logon:function(){
      this.$router.push({path:"/Logon"});
    }
  },
};
</script>
<style scoped>
.logon
{
  font-size:10pt;
}
#Picture_Error {
  position: absolute;
  bottom: -1%;
  right: 3%;
  display: none;
}
#Inputs {
  border-radius: 5pt;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 150pt;
  height: 150pt;
  position: relative;
}
.Pictures {
  width: 20pt;
  height: 16pt;
}
#Content {
  display: flex;
  flex-direction: column;
  height: 90pt;
  position: relative;
}
.inputs {
  border: 1.5px #b3acc1 solid;
  border-radius: 5pt;
  width: 180pt;
  height: 50pt;
  margin-top: 20pt;
  display: flex;
  align-items: center;
  background-color: white;
}
.TrueInputs {
  height: 25pt;
  width: 150pt;
  border: 0px;
  outline: none;
  font-size: 9.5pt;
}
#Button_s {
  display: flex;
  align-items:center;
  justify-content: center;
  flex-direction: column;
  height: 150pt;
  margin-top:20pt;
}
.Button {
  border-radius: 5pt;
  height: 30pt;
  width: 50pt;
  color: white;
  font-size: 12.5pt;
  font-weight: bold;
  letter-spacing: 1pt;
  text-indent: 1pt;
  border: 0px;
}
#Error {
  width: 18pt;
  height: 12pt;
}
#Error_s {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 68%;
  left: 72%;
  width: 100pt;
  height: 20pt;
}
#Error_text {
  font-size: 10pt;
  color: red;
  margin: 0px;
  display: none;
}
</style>